___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --beige: #efe6d5;
    --blue: #9dbeb7;
    --red: #e73213;
    --shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

* {
    padding: 0;
    margin: 0;
    font-weight: 700;
    color: var(--beige);
}

ul {
    list-style-type: none;
}

html {
    height: 100%;
}

body {
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    background-color: var(--beige);
}

button {
    border: 2px solid var(--red);
    padding: 5px;
    border-radius: 10px;
    background-color: var(--red);
    color: var(--beige);
    transition: 0.1s;
    box-shadow: var(--shadow);
}

button:hover {
    background-color: var(--beige);
    color: var(--red);
    transition: 0.1s;
}

header {
    background-color: var(--blue);
    color: var(--beige);
    display: flex;
    justify-content: center;
    padding: 10px 0 10px 0;
    box-shadow: var(--shadow);
}

.projectTaskContainer {
    display: flex;
    padding: 30px;
    height: 100%;
    gap: 10px;
}

#projectForm,
#taskForm {
    position: fixed;
    width: 100vw;
    height: 100vh;
    display: none;
    align-items: center;
    justify-content: center;
    background-color: rgba(46, 46, 46, 0.7);
}

.formWrapper {
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 15px;
    gap: 15px;
    background-color: var(--blue);
    color: var(--red);
}

input,
select {
    border: none;
    border-radius: 5px;
    color: var(--red);
    padding: 5px;
}

textarea {
    border: none;
    padding: 5px;
    border-radius: 5px;
    color: var(--red);
}

label {
    color: var(--red);
}

option {
    color: var(--red);

}

.projectsContainer {
    background-color: var(--blue);
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    padding: 10px;
    box-shadow: var(--shadow);
}

.projectsContainer button {
    width: 100%;
    margin-bottom: 8px;
}

#tasksContainer {
    width: 100%;
    background-color: var(--blue);
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    padding: 10px;
    box-shadow: var(--shadow);
}

.task button {
    margin-right: 10px;
    margin-top: 10px;
}

#tasksList {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 10px;
}

.task,
.project {
    border: 2px solid var(--beige);
    border-radius: 10px;
    background-color: var(--beige);
    padding: 10px;
    box-shadow: var(--shadow);
}

.task p,
.project p {
    color: var(--blue);
}

.project {
    margin-bottom: 8px;
}

.currentProject,
.projectList {
    padding: 10px;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    border-bottom: 2px solid var(--beige);
    padding: 5px;
    border-radius: 10px;
    background-color: var(--beige);
    color: var(--blue);
    box-shadow: var(--shadow);
}

#projectFormMessage,
#taskFormMessage {
    color: var(--red);
    justify-self: center;
}

@media (max-width: 768px) {
    header {
        /* padding: 5px 0 5px 0; */
        padding: 0;
    }

    .projectTaskContainer {
        padding: 5px;
    }

    .projectsContainer {
        width: 100px;
    }

    .task,
    .project {
        padding: 4px;
    }

    #tasksList {
        /* display: grid; */
        grid-template-columns: 1fr;
        grid-gap: 5px;
    }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,gBAAgB;IAChB,eAAe;IACf,cAAc;IACd,yCAAyC;AAC7C;;AAEA;IACI,UAAU;IACV,SAAS;IACT,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,8BAA8B;AAClC;;AAEA;IACI,4BAA4B;IAC5B,YAAY;IACZ,mBAAmB;IACnB,4BAA4B;IAC5B,mBAAmB;IACnB,gBAAgB;IAChB,yBAAyB;AAC7B;;AAEA;IACI,8BAA8B;IAC9B,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,6BAA6B;IAC7B,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IACvB,sBAAsB;IACtB,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,aAAa;IACb,YAAY;IACZ,SAAS;AACb;;AAEA;;IAEI,eAAe;IACf,YAAY;IACZ,aAAa;IACb,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,uCAAuC;AAC3C;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,6BAA6B;IAC7B,iBAAiB;AACrB;;AAEA;;IAEI,YAAY;IACZ,kBAAkB;IAClB,iBAAiB;IACjB,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;;AAErB;;AAEA;IACI,6BAA6B;IAC7B,4BAA4B;IAC5B,+BAA+B;IAC/B,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,6BAA6B;IAC7B,6BAA6B;IAC7B,gCAAgC;IAChC,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,2DAA2D;IAC3D,cAAc;AAClB;;AAEA;;IAEI,8BAA8B;IAC9B,mBAAmB;IACnB,8BAA8B;IAC9B,aAAa;IACb,yBAAyB;AAC7B;;AAEA;;IAEI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;;IAEI,aAAa;IACb,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IACvB,qCAAqC;IACrC,YAAY;IACZ,mBAAmB;IACnB,8BAA8B;IAC9B,kBAAkB;IAClB,yBAAyB;AAC7B;;AAEA;;IAEI,iBAAiB;IACjB,oBAAoB;AACxB;;AAEA;IACI;QACI,0BAA0B;QAC1B,UAAU;IACd;;IAEA;QACI,YAAY;IAChB;;IAEA;QACI,YAAY;IAChB;;IAEA;;QAEI,YAAY;IAChB;;IAEA;QACI,mBAAmB;QACnB,0BAA0B;QAC1B,aAAa;IACjB;AACJ","sourcesContent":[":root {\n    --beige: #efe6d5;\n    --blue: #9dbeb7;\n    --red: #e73213;\n    --shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;\n}\n\n* {\n    padding: 0;\n    margin: 0;\n    font-weight: 700;\n    color: var(--beige);\n}\n\nul {\n    list-style-type: none;\n}\n\nhtml {\n    height: 100%;\n}\n\nbody {\n    height: 